// ** React Imports
import { Fragment, useEffect, useState } from 'react'
import Flatpickr from 'react-flatpickr'
import { Link } from 'react-router-dom'
// ** Reactstrap Imports
import {
  Row,
  Col,
  Card,
  Form,
  Label,
  Input,
  Button,
  CardBody,
  FormFeedback,
  Table,
  Modal,
  ModalHeader,
  ModalBody,
  CardHeader
} from 'reactstrap'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import { useForm, Controller } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
import { getData, addHolidays, updateHolidays, deleteHolidays } from './store/holidays'
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'

import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

const activeOrgId = activeOrganizationid()
const userId = orgUserId()
// ** Third Party Components
import classnames from 'classnames'
import { useDispatch, useSelector } from 'react-redux'
import moment from 'moment'
import ReactPaginate from 'react-paginate'
import toast from 'react-hot-toast'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'

const Holidays = () => {

  const MySwal = withReactContent(Swal)
  const [data, setData] = useState([])
  const [selected, setSelected] = useState(null)
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const rowsPerPage = 10
  const [currentPage, setCurrentPage] = useState(1)
  const sortColumn = 'date'
  const store = useSelector(state => state.holidays)
  const dispatch = useDispatch()

  const [formModal, setFormModal] = useState(false)

  const schema = yup.object().shape({
    organizationId: yup.string().default(activeOrgId),
    name: yup.string().required('Please Enter Name'),
    date: yup.string().required('Please Select Date'),
    comments: yup.string().nullable().default(''),
    status: yup.boolean().default(true),
    updatedBy: yup.string().default(userId),
    createdBy: yup.string().default(userId)
  })

  const { handleSubmit, formState: { errors }, control, setValue } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const closeModal = () => {
    setFormModal(false)
  }

  const resetForm = () => {
    setValue('name', '')
    setValue('date', '')
    setValue('comments', '')
    setValue('status', true)
    setValue('id', '')
    setValue('organizationId', activeOrgId)
    setValue('createdBy', userId)
    setValue('updatedBy', userId)

    closeModal()
  }

  useEffect(() => {
    setFormModal(false)
  }, [])

  const dateFormat = (value) => {
    if (value.length === 13) {
      return moment(value, 'x').format("MMM DD, YYYY")
    } else {
      return moment.unix(value).format("MMM DD, YYYY")
    }
  }

  const handlePagination = async page => {
    await dispatch(getData({
      sort: sortColumn,
      page: page.selected + 1
    }))

    setCurrentPage(page.selected + 1)

  }

  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
      />
    )
  }

  const deletefn = (id) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteHolidays(id))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Holidays has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
        return true
      } else if (result.dismiss === MySwal.DismissReason.cancel) {
        return false
      }
    })
  }

  const onSubmit = async data => {
    data.date = moment(data.date, 'MMM D, Y').startOf('day').valueOf()
    setBlock(true)
    if (selected !== null) {
      await dispatch(updateHolidays(data))
      toast.success('Holidays Updated Successfully', { position: 'bottom-right' })
      setSelected(null)
    } else {
      await dispatch(addHolidays(data))
      toast.success('Holidays Added Successfully', { position: 'bottom-right' })
    }
    resetForm()
    setBlock(false)
  }

  useEffect(async () => {
    await dispatch(getData({
      sort: sortColumn,
      page: currentPage
    }))
  }, [])

  useEffect(() => {
    setData(store.data)
  }, [store.data])

  const getRow = (fieldLabel, fieldName, reqflag = true) => {
    return (
      <Col md={12}>
        <Label sm='12' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName}>
          {fieldLabel}
        </Label>
        <Col>
          <Controller
            id={fieldName}
            name={fieldName}
            control={control}
            render={({ field }) => <Input invalid={errors[fieldName] && true} {...field} />}
          />
          {errors[fieldName] && <FormFeedback>{errors[fieldName].message}</FormFeedback>}
        </Col>
      </Col>
    )
  }

  const openEditModal = card => {
    setSelected(card)
    setFormModal(true)
    const date = moment(card.date, 'x').format('MMM D, Y')
    setValue('name', card.name)
    setValue('date', date)
    setValue('id', card.id)
    setValue('comments', card.comment)
    setValue('status', card.status)
  }

  return (
    <Fragment>
      <Card>
        <CardHeader>
          <Row className='gx-4'>
            <Col className='d-flex align-items-center'>
              <h6 className='fw-bolder mb-2'>Holidays</h6>
            </Col>
            <Col className='d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1'>
              <Button color='primary' outline className='ms-1' onClick={() => { setFormModal(true) }}>
                Add
              </Button>
              <Button color='primary' outline className='ms-1' tag={Link} to='/import/holidays'>
                Import
              </Button>
            </Col>
          </Row>
        </CardHeader>
        <CardBody className='py-25'>
          <Table>
            <thead>
              <tr>
                <th>Name</th>
                <th>Date</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
              {data.map((card, index) => {
                return (
                  <tr key={index}>
                    <td>
                      {card.name}
                    </td>
                    <td>
                      {dateFormat(card.date)}
                    </td>
                    <td>
                      <Button size='sm' outline color='primary' className='me-75' onClick={() => openEditModal(card)}>
                        Edit
                      </Button>
                      {!card.isPrimary &&
                        <Button size='sm' outline onClick={() => { deletefn(card.id) }}>Delete</Button>
                      }
                    </td>
                  </tr>
                )
              })}
            </tbody>
          </Table>
          <CustomPagination />

        </CardBody>
      </Card>
      <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered'>
        <UILoader blocking={block} loader={<Loader />}>
          <ModalHeader toggle={() => closeModal()}>Holidays</ModalHeader>
          <ModalBody>
            {
              Object.keys(errors).map((key) => {

                return <FormFeedback key={key}>${errors[key]?.message}</FormFeedback>

              })
            }
            <Row tag={Form} className='gx-2 gy-1' onSubmit={handleSubmit(onSubmit)}>

              {getRow('Name', 'name', true)}

              <Col md={12}>
                <Label className='form-label required' for='comments'>
                  Date
                </Label>
                <Controller
                  control={control}
                  name={`date`}
                  id='date'
                  render={({ field }) => (
                    <Flatpickr
                      value={field.value}
                      onChange={(date, dateStr) => { field.onChange(dateStr) }}
                      options={{ altInput: true, altFormat: "M j, Y", dateFormat: "M j, Y" }}
                      className='form-control invoice-edit-input date-picker'
                    />
                  )}
                />
                {errors.date && <FormFeedback>{errors.date.message}</FormFeedback>}

              </Col>

              <Col md={12}>
                <Label className='form-label' for='comments'>
                  Comments
                </Label>
                <Controller
                  id='comments'
                  name='comments'
                  control={control}
                  render={({ field }) => <Input type="textarea" invalid={errors.comments && true} {...field} />}
                />
                {errors.comments && <FormFeedback>{errors.comments.message}</FormFeedback>}

              </Col>

              <Col className='mt-2 pt-1' xs={12}>
                <Button type='submit' className='me-1' color='primary'>
                  Submit
                </Button>
                <Button className='me-1' color='warning' onClick={() => closeModal()}>
                  Cancel
                </Button>
              </Col>
            </Row>
          </ModalBody>
        </UILoader>
      </Modal>
    </Fragment>
  )
}

export default Holidays
